@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap);
@font-face {
  font-family: "Raleway";
  src: local("Raleway"),
    url(/static/media/Raleway-VariableFont_wght.3b5635ac.ttf) format("truetype");
}

html {
  font-size: calc(1em+1vh);
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: "Raleway";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: overlay;
}
body::-webkit-scrollbar {
  width: 0.3em;
}

body::-webkit-scrollbar-track {
  background: rgba(61, 50, 28, 0);
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(150, 150, 150, 0.3);
  border-radius: 20px;
  padding: 0.1;
}

.jay-pic {
  position: absolute;
  object-fit: contain;
  height: 100vmin;
  right: 0;
  bottom: 0;
}
.typoHeader {
  font-family: "Anton";
  z-index: -1;
}

.jay-container {
  height: 100%;
  text-align: right;
  z-index: 1;
}
.landing-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
}
.scroll-btn {
  bottom: 0;
  position: absolute;
  padding: 1rem;
  z-index: 2;
}

.side-menu {
  position: absolute;
  right: 0;
  width: 10rem;
  height: 100vh;
  background-color: #fbd630;
  -webkit-clip-path: circle(0 at 100% 0);
          clip-path: circle(0 at 100% 0);
}

@media only screen and (max-width: 1280px) {
  .landing-container {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
}

